<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 请选择公司 start -->
          <el-form-item label="" prop="companyId">
            <el-select
              v-model="searchValidateForm.companyId"
              filterable
              placeholder="请选择公司"
              clearable
              @change="onChangeSearchCompany"
            >
              <el-option
                v-for="(item, index) in searchCompanyOptions"
                :key="index"
                :label="item.COMPANY_NAME"
                :value="item.COMPANY_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择公司 end -->

          <!-- 请选择景区 start -->
          <el-form-item label="" prop="spotId">
            <el-select
              v-model="searchValidateForm.spotId"
              filterable
              placeholder="请选择景区"
              clearable
              @change="onChangeSearchSpot"
            >
              <el-option
                v-for="(item, index) in searchScenicOptions"
                :key="index"
                :label="item.SPOT_NAME"
                :value="item.SPOT_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择景区 end -->

          <!-- 请选择日期统计方式 start -->
          <!-- <el-form-item label="" prop="">
            <el-select
              v-model="selectDateState"
              filterable
              placeholder="请选择日期统计方式"
              @change="onDateChange"
            >
              <el-option
                v-for="(item, index) in dateStateOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item> -->
          <!-- 请选择日期统计方式 end -->

          <!-- 输入起止时间 start -->
          <el-form-item label="" prop="">
            <el-date-picker
              v-model="searchStartEndDate"
              unlink-panels
              :type="dateType"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            >
            </el-date-picker>
          </el-form-item>
          <!-- 输入起止时间 end -->

          <!-- 请选择状态 start -->
          <el-form-item label="" prop="payState">
            <el-select
              v-model="searchValidateForm.payState"
              filterable
              placeholder="请选择状态"
            >
              <el-option
                v-for="(item, index) in payStateOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择状态 end -->
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 统计毛利 start -->
        <!-- <el-button
          type="primary"
          icon="el-icon-data-line"
          @click="onStatGrossProfit"
          >统计毛利</el-button
        > -->
        <!-- 统计毛利 end -->

        <!-- 导出报表 start -->
        <!-- <a href="" id="exportReport" download="景区分润统计">
          <el-button
            type="primary"
            icon="el-icon-s-data"
            @click="onExportReport"
            >导出报表</el-button
          >
        </a> -->
        <!-- 导出报表 end -->

        <!-- 查询数据 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >查询数据</el-button
        >
        <!-- 查询数据 end -->

        <!-- 导出 Excel start -->
        <el-button
          type="primary"
          :loading="downloadLoading"
          icon="el-icon-top-right"
          @click="exportExcel"
          >导出数据</el-button
        >

        <!-- 导出 Excel end -->

        <!-- 新增 start -->
        <!-- <el-button type="primary" icon="el-icon-circle-plus-outline" @click="onAdd">新增</el-button> -->
        <!-- 新增 end -->
      </el-header>
      <!-- 显示表格 start -->
      <div v-show="showTable">
        <el-main>
          <!-- 表格 start -->
          <el-table
            class="table"
            :data="tableData"
            style="width: 100%"
            border
            v-loading="tableLoading"
            show-summary
            :summary-method="getSummaries"
          >
            <template v-for="(value, key) in tableFields">
              <el-table-column
                :key="key"
                :prop="value"
                :label="key"
                min-width="130"
                :fixed="activeFixed(value)"
              >
                <template slot-scope="scope">
                  <div>
                    {{ tableContentValue(scope.row, value) }}
                  </div>
                </template>
              </el-table-column>
            </template>

            <el-table-column label="操作" min-width="130" fixed="right">
              <template slot-scope="scope">
                <el-button
                  @click.native.prevent="editRow(scope.$index, scope.row)"
                  type="text"
                  size="small"
                >
                  编辑
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <!-- 表格 end -->

          <!-- 导出的表格 start -->
          <!-- 此表格始终保持隐藏 -->
          <el-table
            id="outTable"
            class="table"
            :data="exportTableData"
            style="width: 100%; display: none"
            border
            :header-cell-style="{
              background: '#f2f2f2',
              color: '#606266',
              height: '38px',
              padding: '0px'
            }"
            :cell-style="{ height: '38px', padding: '0' }"
            show-summary
            :summary-method="getSummaries"
          >
            <el-table-column
              v-for="(value, key) in tableFields"
              :key="key"
              :prop="value"
              :label="key"
              min-width="130"
              align="center"
            >
            </el-table-column>
          </el-table>
          <!-- 导出的表格 end -->
        </el-main>
        <el-footer>
          <!-- 分页 start -->
          <div class="block">
            <el-pagination
              background
              layout="prev, pager, next, total, sizes, jumper"
              :total="total"
              :current-page="currentPage"
              :page-sizes="[10, 20, 30, 40, 50, 60, 70, 80, 90]"
              :page-size="pageSize"
              @current-change="onCurrentChange"
              @size-change="onSizeChange"
            >
            </el-pagination>
          </div>
          <!-- 分页 end -->
        </el-footer>
      </div>
      <!-- 显示表格 end -->

      <!-- 显示柱状图 start -->
      <div v-show="!showTable">
        <div class="my-echarts">
          <div id="myChart"></div>
        </div>
      </div>
      <!-- 显示柱状图 end -->

      <!-- dialog对话框 start -->
      <el-dialog
        class="dialog"
        title="编辑景区分润统计"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
        :close-on-click-modal="false"
      >
        <!-- form表单 -->
        <el-form
          :model="validateForm"
          ref="validateForm"
          label-width="100px"
          class="demo-ruleForm"
          :rules="validateFormRules"
        >
          <!-- 平台统计分润基数/元 start -->
          <el-form-item label="平台微信支付金额" prop="wechatPaymentFlow">
            <el-input
              type="number"
              v-model="validateForm.wechatPaymentFlow"
              autocomplete="off"
              placeholder="请输入平台微信支付金额"
            ></el-input>
          </el-form-item>
          <!-- 平台微信支付金额/元 end -->

          <!-- 平台储值支付金额/元 start -->
          <el-form-item label="平台储值支付金额" prop="storedValuePaymentFlow">
            <el-input
              type="number"
              v-model="validateForm.storedValuePaymentFlow"
              autocomplete="off"
              placeholder="请输入平台储值支付金额"
            ></el-input>
          </el-form-item>
          <!-- 平台储值支付金额/元 end -->

          <!-- 平台储值抵扣+微信支付金额/元 start -->
          <el-form-item label="平台储值抵扣+微信支付金额" prop="wechatStoredPaymentFlow">
            <el-input
              type="number"
              v-model="validateForm.wechatStoredPaymentFlow"
              autocomplete="off"
              placeholder="请输入平台储值抵扣+微信支付金额"
            ></el-input>
          </el-form-item>
          <!-- 平台储值抵扣+微信支付金额/元 end -->

          <!-- 平台押金抵扣金额/元 start -->
          <el-form-item label="平台押金抵扣金额" prop="depositDeductionPaymentFlow">
            <el-input
              type="number"
              v-model="validateForm.depositDeductionPaymentFlow"
              autocomplete="off"
              placeholder="请输入平台押金抵扣金额"
            ></el-input>
          </el-form-item>
          <!-- 平台押金抵扣金额/元 end -->

          <!-- 平台总订单金额/元 start -->
          <el-form-item label="平台总订单金额" prop="totalAmount">
            <el-input
              type="number"
              v-model="validateForm.totalAmount"
              autocomplete="off"
              placeholder="请输入平台总订单金额"
            ></el-input>
          </el-form-item>
          <!-- 平台总订单金额/元 end -->

          <!-- 平台统计分润基数/元 start -->
          <el-form-item label="平台统计分润基数" prop="platformFenrunBase">
            <el-input
              type="number"
              v-model="validateForm.platformFenrunBase"
              autocomplete="off"
              placeholder="请输入平台统计分润基数"
            ></el-input>
          </el-form-item>
          <!-- 平台统计分润基数/元 end -->

          <!-- 平台统计分润(支出)金额/元 start -->
          <el-form-item
            label="平台统计分润(支出)金额"
            prop="platformFenrunExpenditure"
          >
            <el-input
              type="number"
              v-model="validateForm.platformFenrunExpenditure"
              autocomplete="off"
              placeholder="请输入平台统计分润(支出)金额"
            ></el-input>
          </el-form-item>
          <!-- 平台统计分润(支出)金额/元 end -->

          <!-- 平台统计分润(收入)金额/元 start -->
          <el-form-item
            label="平台统计分润(收入)金额"
            prop="platformFenrunIncome"
          >
            <el-input
              type="number"
              v-model="validateForm.platformFenrunIncome"
              autocomplete="off"
              placeholder="请输入平台统计分润(收入)金额"
            ></el-input>
          </el-form-item>
          <!-- 平台统计分润(收入)金额/元 end -->

          <!-- 平台统计配件维修金额/元 start -->
          <el-form-item
            label="平台统计配件维修金额"
            prop="platformStatisticsPartsMaintenance"
          >
            <el-input
              type="number"
              v-model="validateForm.platformStatisticsPartsMaintenance"
              autocomplete="off"
              placeholder="请输入平台统计配件维修金额"
            ></el-input>
          </el-form-item>
          <!-- 平台统计配件维修金额/元 end -->

          <!-- 平台统计结算(支出)金额/元 start -->
          <el-form-item
            label="平台统计结算(支出)金额"
            prop="platformStatisticsSettlementExpenditure"
          >
            <el-input
              type="number"
              v-model="
                validateForm.platformStatisticsSettlementExpenditure
              "
              autocomplete="off"
              placeholder="请输入平台统计结算(支出)金额"
            ></el-input>
          </el-form-item>
          <!-- 平台统计结算(支出)金额/元 end -->

          <!-- 平台统计结算(收入)金额/元 start -->
          <el-form-item
            label="平台统计结算(收入)金额"
            prop="platformStatisticsSettlementIncome"
          >
            <el-input
              type="number"
              v-model="validateForm.platformStatisticsSettlementIncome"
              autocomplete="off"
              placeholder="请输入平台统计结算(收入)金额"
            ></el-input>
          </el-form-item>
          <!-- 平台统计结算(收入)金额/元 end -->

          <!-- 对账备注 start -->
          <el-form-item label="对账备注" prop="reconciliationRemarks">
            <el-input
              type="text"
              v-model="validateForm.reconciliationRemarks"
              autocomplete="off"
              placeholder="请输入对账备注"
            ></el-input>
          </el-form-item>
          <!-- 对账备注 end -->
        </el-form>
        <!-- form表单 -->
        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="submitForm('validateForm')"
            >提交</el-button
          >
          <el-button @click="dialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- dialog对话框 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { formatDateTime, formatMonth } from '../utils/utils.js'
export default {
  name: 'ScenicProfitStatistic',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      tableLoading: false,
      showTable: true, // 是否显示表格
      feeInput: '', // 请输入手续费
      taxInput: '', // 请输入税点
      searchFeeInput: '', // 搜索的手续费
      searchTaxInput: '', // 搜索的税点
      tableFields: {
        //导出Excel表格的表头设置
        合作主体: 'subjectName',
        合作公司: 'companyName',
        收款单位: 'payee',
        景区名称: 'scenicSpotName',
        月份: 'profitSharingMonth',
        '平台微信支付金额/元': 'wechatPaymentFlow',
        '平台储值支付金额/元': 'storedValuePaymentFlow',
        '平台储值抵扣+微信支付金额/元': 'wechatStoredPaymentFlow',
        '平台押金抵扣金额/元': 'depositDeductionPaymentFlow',
        '平台总订单金额/元': 'totalAmount',
        '平台统计分润基数/元': 'platformFenrunBase',
        '平台统计分润(支出)金额/元': 'platformFenrunExpenditure',
        '平台统计分润(收入)金额/元': 'platformFenrunIncome',
        '平台统计配件维修金额/元': 'platformStatisticsPartsMaintenance',
        '平台统计结算(支出)金额/元': 'platformStatisticsSettlementExpenditure',
        '平台统计结算(收入)金额/元': 'platformStatisticsSettlementIncome',
        收支类型: 'type',
        手续费费率: 'charge',
        分润比例: 'proportion',
        税点: 'taxPoint',
        // 毛利: 'grossProfit',
        // 毛利率: 'grossMargin',
        // 台均毛利: 'avgGrossProfit',
        // "状态": "state",
        对账备注: 'reconciliationRemarks',
        '实际分润基数金额/元': 'actualFenrunBase',
        '实际扣税后分润(支出)金额/元': 'actualTaxFenrunExpenditure',
        '实际扣税后分润(收入)金额/元': 'actualTaxFenrunIncome',
        分润金额结算状态: 'fenrunSettlemenType',
        '景区承担的配件维修金额/元': 'spotPartsMaintenance',
        配件维修费结算状态: 'spotSettlemenType',
        // "实际配件维修金额/元": "actualPartRepairAmount",
        '实际结算(支出)金额/元': 'actualSettlemenExpenditure',
        '实际结算(收入)金额/元': 'actualSettlemenIncome',
        分润备注: 'fenrunRemarks'
      },
      tableData: [], // 表格数据
      totalData: {}, // 合计数据

      searchCompanyOptions: [], // 搜索的公司选项
      searchScenicOptions: [], // 搜索的景区选项

      companyOptions: [], // 提交的公司选项
      scenicOptions: [], // 提交的景区选项

      dateStateOptions: [
        {
          value: '1',
          label: '按月查询'
        },
        {
          value: '2',
          label: '按日查询'
        },
        {
          value: '3',
          label: '按年查询'
        }
      ], // 日期统计方式选项
      payStateOptions: [
        {
          value: '1',
          label: '已核实'
        },
        {
          value: '2',
          label: '已付款'
        },
        {
          value: '3',
          label: '已收款'
        }
      ], // 景区选项

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      total: 0, // 总页数

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示编辑dialog对话框

      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      selectDateState: '1', // 日期统计方式（月，日，年）
      dateType: 'monthrange', // 日期类型（月，日，年）
      searchValidateForm: {
        partnerCompanyName: '', // 公司名称
        scenicName: '', // 景区名称
        startEndDate: '', // 起止日期
        payState: '' // 支付状态
      }, // 搜索表单验证对象
      validateForm: {}, // 表单验证对象

      searchPartnerCompanyName: '', // 搜索的公司
      searchScenicName: '', // 搜索的景区
      searchSelectDateState: '1', // 搜索的日期统计方式
      searchStartEndDate: [new Date(), new Date()], // 搜索的起止时间
      searchPayState: '', // 搜索的支付状态

      companys: [], // 公司列表
      companyState: '', // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: '', // 当前请选择景区

      // 搜索表单规则
      searchValidateFormRules: {},

      // 提交表单验证
      validateFormRules: {
        // 合作主体
        subjectName: [
          {
            validator: checkRequireMethod('合作主体'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],
      }
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }
    this.getSearchValidateForm()
    this.getValidateForm()

    this.initTableData()
    this.getCompanyList()
    this.getScenicList()
  },
  mounted() {
    this.drawEcharts()
  },
  methods: {
    // 获取搜索表单验证对象
    getSearchValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = null
      }
      this.searchValidateForm = {
        ...validateForm,
        companyId: null,
        spotId: null
      }
    },

    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = null
      }
      this.validateForm = {
        ...validateForm,
        companyId: null,
        spotId: null
      }
      console.log(this.validateForm)
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 根据景区ID获取公司列表（搜索时公司列表）
     */
    async getCompanyList(spotId) {
      if (!spotId) {
        spotId = null
      }
      const { data: res } = await this.$http.get(
        '/system/subscriptionInformation/companyList',
        {
          params: {
            spotId: spotId
          }
        }
      )
      if (res.state == '200') {
        // console.log(res);
        this.searchCompanyOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 根据景区ID获取公司列表（表单操作搜索时公司列表）
     */
    async getFormCompanyList(spotId) {
      if (!spotId) {
        spotId = null
      }
      const { data: res } = await this.$http.get(
        '/system/subscriptionInformation/companyList',
        {
          params: {
            spotId: spotId
          }
        }
      )
      if (res.state == '200') {
        // console.log(res);
        this.companyOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 根据公司ID获取景区列表（表单提交搜索时景区列表）
     */
    async getFormScenicList(companyId) {
      if (!companyId) {
        companyId = null
      }
      // 这里使用的是`签约信息`中的`景区列表`接口
      const { data: res } = await this.$http.get(
        '/system/subscriptionInformation/spotList',
        {
          params: {
            companyId: companyId
          }
        }
      )
      if (res.state == '200') {
        // console.log(res);
        this.scenicOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 根据公司ID获取景区列表（搜索时景区列表）
     */
    async getScenicList(companyId) {
      if (!companyId) {
        companyId = null
      }
      // 这里使用的是`签约信息`中的`景区列表`接口
      const { data: res } = await this.$http.get(
        '/system/subscriptionInformation/spotList',
        {
          params: {
            companyId: companyId
          }
        }
      )
      if (res.state == '200') {
        // console.log(res);
        this.searchScenicOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      this.tableLoading = true

      var startTime = null
      var endTime = null

      if (this.searchStartEndDate && this.searchStartEndDate.length !== 0) {
        startTime = formatDateTime(
          this.searchStartEndDate[0],
          this.selectDateState
        )
        endTime = formatDateTime(
          this.searchStartEndDate[1],
          this.selectDateState
        )
      }

      const { data: res } = await this.$http.get(
        '/system/spotProfitSharingStatistics/spotProfitSharingStatisticeList',
        {
          params: {
            ...this.searchValidateForm,
            startDate: startTime,
            endDate: endTime,
            pageNum: this.currentPage, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
      // console.log(res)
      if (res.code != 200) {
        this.$message({
          type: 'error',
          message: '请求失败！'
        })
        return
      }
      this.totalData = (res.data && res.data[0]) || {}

      this.tableData = res.list
      this.total = res.totals

      this.tableLoading = false
    },

    /**
     * 修改表格数据
     */
    async editTableData() {
      const { data: res } = await this.$http.post(
        '/system/spotProfitSharingStatistics/editSpotProfitSharingStatistice',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
      this.downloadLoading = true

      var startTime = null
      var endTime = null

      if (this.searchStartEndDate && this.searchStartEndDate.length !== 0) {
        startTime = formatDateTime(
          this.searchStartEndDate[0],
          this.selectDateState
        )
        endTime = formatDateTime(
          this.searchStartEndDate[1],
          this.selectDateState
        )
      }

      const { data: res } = await this.$http.get(
        '/system/spotProfitSharingStatistics/downloadExcel',
        {
          params: {
            ...this.searchValidateForm,
            startDate: startTime,
            endDate: endTime,
          },
          responseType: 'arraybuffer'
        }
      )
      // console.log(res)
      const url = window.URL.createObjectURL(
        new Blob([res], {
          type:
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
        })
      )
      const a = document.createElement('a')
      console.log(url)
      a.href = url
      a.download = '景区分润统计'
      a.click()
      this.downloadLoading = false
    },

    /**
     * 获取景区分润统计图表数据
     */
    async getSpotGrossProfitMarginChartList() {
      var startTime = null
      var endTime = null

      if (this.searchStartEndDate && this.searchStartEndDate.length !== 0) {
        startTime = formatDateTime(
          this.searchStartEndDate[0],
          this.selectDateState
        )
        endTime = formatDateTime(
          this.searchStartEndDate[1],
          this.selectDateState
        )
      }

      // 加载Echarts loading效果
      this.myChart.showLoading({
        text: 'loading',
        color: '#c23531',
        textColor: '#000',
        maskColor: 'rgba(255, 255, 255, 0.2)',
        zlevel: 0
      })

      const { data: res } = await this.$http.get(
        '/system/spotProfitSharingStatistics/spotProfitSharingStatisticeChart',
        {
          params: {
            ...this.searchValidateForm,
            startDate: startTime,
            endDate: endTime
          }
        }
      )
      console.log(res)

      this.myChart.hideLoading()

      var data = res.data || {}

      this.option.xAxis.data = data.spotNameList
      this.option.series[0].data = data.grossProfitList
      this.option.series[1].data = data.grossProfitMargin
      this.option.series[2].data = data.robotGrossProfit
      this.myChart.setOption(this.option)
    },

    /**
     * 画Echarts图表(柱状图)
     */
    drawEcharts() {
      this.myChart = this.$echarts.init(document.getElementById('myChart'))

      this.option = {
        backgroundColor: '#fff', //背景色
        tooltip: {
          trigger: 'axis',
          textStyle: {
            align: 'left'
          },
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data: ['毛利', '毛利率', '台均毛利'],
          top: 0 // 把legend放到柱状图上面
        },
        dataZoom: [
          {
            type: 'inside',
            start: 0,
            end: 10
          },
          {
            type: 'slider'
            // show: true,
            // start: dataAxis.length - 6,
            // end: dataAxis.length - 1,
          }
        ],
        xAxis: {
          type: 'category',
          data: [],
          axisPointer: {
            type: 'shadow'
          }
        },
        yAxis: [
          {
            name: '毛利',
            type: 'value',
            axisLabel: {
              formatter: '{value} 元'
            }
          },
          {
            name: '毛利率',
            type: 'value',
            axisLabel: {
              formatter: '{value} %'
            }
          }
        ],
        series: [
          {
            name: '毛利',
            type: 'bar',
            data: [],
            tooltip: {
              valueFormatter: function(value) {
                return value + ' 元'
              }
            }
          },
          {
            name: '毛利率',
            type: 'line',
            yAxisIndex: 1,
            data: [],
            tooltip: {
              valueFormatter: function(value) {
                return value + ' %'
              }
            }
          },
          {
            name: '台均毛利',
            type: 'bar',
            // yAxisIndex: 1,
            data: [],
            tooltip: {
              valueFormatter: function(value) {
                return value + ' 元'
              }
            }
          }
        ]
      }

      this.myChart.setOption(this.option)

      window.addEventListener('resize', () => {
        this.myChart.resize()
      })
    },

    /** 搜索表单中选项变化方法 start */
    // 若先选择公司名称，则关联显示可选择的景区名称
    async onChangeSearchCompany(companyId) {
      // console.log(companyId);
      var res = await this.getScenicList(companyId)
      console.log(res)
      var searchScenicOptions = res.data

      this.searchScenicOptions = searchScenicOptions

      // 如果返回的景区列表为空，则直接返回
      if (searchScenicOptions.length == 0) {
        this.searchValidateForm.spotId = ''
        return
      }

      var isHasScenic = false
      searchScenicOptions.forEach((item) => {
        if (item.SPOT_ID == this.searchValidateForm.spotId) {
          isHasScenic = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则景区，则为搜索景区选择框设置默认值
      if (!!companyId && !isHasScenic) {
        this.searchValidateForm.spotId = searchScenicOptions[0].SPOT_ID
      }
    },

    // 景区选项变化时触发
    async onChangeSearchSpot(spotId) {
      var res = await this.getCompanyList(spotId)
      // console.log(res.data);
      var searchCompanyOptions = res.data

      this.searchCompanyOptions = searchCompanyOptions

      // 如果返回的公司列表为空，则直接返回
      if (searchCompanyOptions.length == 0) {
        this.searchValidateForm.companyId = ''
        return
      }

      var isHasCompany = false
      searchCompanyOptions.forEach((item) => {
        if (item.COMPANY_ID == this.searchValidateForm.companyId) {
          isHasCompany = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则公司，则为搜索公司选择框设置默认值
      if (!!spotId && !isHasCompany) {
        this.searchValidateForm.companyId = searchCompanyOptions[0].COMPANY_ID
      }
    },

    /** 搜索表单中选项变化方法 end */

    // 日期选项值发生变化时触发
    onDateChange() {
      console.log(this.selectDateState)
      if (this.selectDateState == '1') {
        // 按月查询
        this.dateType = 'monthrange'
      } else if (this.selectDateState == '2') {
        // 按日查询
        this.dateType = 'daterange'
      } else if (this.selectDateState == '3') {
        // 按年查询
        this.dateType = 'yearrange'
      }
    },

    // 重置
    onReset() {
      // 清空所有输入框内容
      this.getSearchValidateForm()
      this.currentPage = 1

      this.searchStartEndDate = [new Date(), new Date()]
      this.selectDateState = '1'
      this.dateType = 'monthrange'

      // 重新获取表格数据
      this.initTableData()
      // 重新获取搜索公司列表
      this.getCompanyList()
      // 重新获取搜索景区列表
      this.getScenicList()
    },

    // 统计毛利(显示柱状图)
    onStatGrossProfit() {
      this.showTable = false
      // 获取图表数据
      this.getSpotGrossProfitMarginChartList()
    },

    // 导出报表（导出柱状图）
    onExportReport() {
      this.$nextTick(() => {
        var exportReportEle = document.getElementById('exportReport')
        exportReportEle.href = this.myChart.getDataURL()
        // exportReportEle.click()
      })
    },
    // 导出报表（导出折线图）
    onExportReportLine() {
      this.$nextTick(() => {
        var exportReportEle = document.getElementById('exportReportLine')
        exportReportEle.href = this.myLineChart.getDataURL()
        console.log(exportReportEle)
        // exportReportEle.click()
      })
    },

    // 点击查询数据按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.showTable = true
          // console.log(this.searchValidateForm);
          this.initTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 导出Excel表格(点击导出数据按钮触发)
    exportExcel() {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.getExportTableData()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 新增
    onAdd() {
      this.dialogVisible = true
      this.dialogState = 'add'
    },

    // 点击编辑时触发
    editRow(index, row) {
      console.log(index, row)
      this.dialogVisible = true // 是否显示编辑dialog对话框

      var rowObj = Object.assign({}, row) // 深拷贝row对象
      rowObj.totalAmount = parseInt(rowObj.totalAmount)
      rowObj.charge = parseInt(rowObj.charge)
      rowObj.dividableAmount = parseInt(rowObj.dividableAmount)
      rowObj.profitSharingRatio = parseInt(rowObj.profitSharingRatio)
      rowObj.profitSharingAmount = parseInt(rowObj.profitSharingAmount)
      rowObj.taxPoint = parseInt(rowObj.taxPoint)
      rowObj.taxDeStreamProfitSharingExpenditureAmount = parseInt(
        rowObj.taxDeStreamProfitSharingExpenditureAmount
      )
      rowObj.robotNumber = parseInt(rowObj.robotNumber)
      rowObj.accessoryRepairAmount = parseInt(rowObj.accessoryRepairAmount)
      rowObj.payment = parseInt(rowObj.payment)
      this.validateForm = rowObj
      this.dialogState = 'edit'
    },

    // dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.validateForm)
          if (this.dialogState == 'add') {
            // 新增公司信息
            console.log('新增公司信息')
          } else if (this.dialogState == 'edit') {
            // 编辑公司信息
            console.log('编辑公司信息')
            this.editTableData()
          }
          this.dialogVisible = false
          this.$refs.validateForm.resetFields() // 重置表单
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    /**
     * 自定义合计行
     */
    getSummaries(param) {
      const { columns, data } = param
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '合计'
          // return
        }

        // console.log(column)

        var data = this.totalData || {}

        // 平台微信支付金额
        if (column.property == 'wechatPaymentFlow') {
          sums[index] = data.wechatPaymentFlowTotal && Number(data.wechatPaymentFlowTotal).toFixed(2) || '0.00' + '元'
        }
        // 平台储值支付金额
        if (column.property == 'storedValuePaymentFlow') {
          sums[index] = data.storedValuePaymentFlowTotal && Number(data.storedValuePaymentFlowTotal).toFixed(2) || '0.00' + '元'
        }
        // 平台储值抵扣+微信支付金额
        if (column.property == 'wechatStoredPaymentFlow') {
          sums[index] = data.wechatStoredPaymentFlowTotal && Number(data.wechatStoredPaymentFlowTotal).toFixed(2) || '0.00' + '元'
        }
        // 平台押金抵扣金额
        if (column.property == 'depositDeductionPaymentFlow') {
          sums[index] = data.depositDeductionPaymentFlowTotal && Number(data.depositDeductionPaymentFlowTotal).toFixed(2) || '0.00' + '元'
        }
        // 平台总订单金额
        if (column.property == 'totalAmount') {
          sums[index] = data.totalAmountTotal && Number(data.totalAmountTotal).toFixed(2) || '0.00' + '元'
        }
        // 平台统计分润基数
        if (column.property == 'platformFenrunBase') {
          sums[index] = data.platformFenrunBaseTotal && Number(data.platformFenrunBaseTotal).toFixed(2) || '0.00' + '元'
        }
        // 平台统计分润(支出)金额
        if (column.property == 'platformFenrunExpenditure') {
          sums[index] = data.platformFenrunExpenditureTotal || 0 + '元'
        }
        // 平台统计分润(收入)金额
        if (column.property == 'platformFenrunIncome') {
          sums[index] = data.platformFenrunIncomeTotal && Number(data.platformFenrunIncomeTotal).toFixed(2) || '0.00' + '元'
        }
        // 平台统计配件维修金额
        if (column.property == 'platformStatisticsPartsMaintenance') {
          sums[index] = data.platformStatisticsPartsMaintenanceTotal && Number(data.platformStatisticsPartsMaintenanceTotal).toFixed(2) || '0.00' + '元'
        }
        // 平台统计结算(支出)金额
        if (column.property == 'platformStatisticsSettlementExpenditure') {
          sums[index] =
            data.platformStatisticsSettlementExpenditureTotal && Number(data.platformStatisticsSettlementExpenditureTotal).toFixed(2) || '0.00' + '元'
        }
        // 平台统计结算(收入)金额
        if (column.property == 'platformStatisticsSettlementIncome') {
          sums[index] = data.platformStatisticsSettlementIncomeTotal && Number(data.platformStatisticsSettlementIncomeTotal).toFixed(2) || '0.00' + '元'
        }
        // 实际分润基数金额
        if (column.property == 'actualFenrunBase') {
          sums[index] = data.actualFenrunBaseTotal && Number(data.actualFenrunBaseTotal).toFixed(2) || '0.00' + '元'
        }
        // 实际扣税后分润(支出)金额
        if (column.property == 'actualTaxFenrunExpenditure') {
          sums[index] = data.actualTaxFenrunExpenditureTotal && Number(data.actualTaxFenrunExpenditureTotal).toFixed(2) || '0.00' + '元'
        }
        // 实际扣税后分润(收入)金额
        if (column.property == 'actualTaxFenrunIncome') {
          sums[index] = data.actualTaxFenrunIncomeTotal && Number(data.actualTaxFenrunIncomeTotal).toFixed(2) || '0.00' + '元'
        }
        // 景区承担的配件维修金额
        if (column.property == 'spotPartsMaintenance') {
          sums[index] = data.spotPartsMaintenanceTotal && Number(data.spotPartsMaintenanceTotal).toFixed(2) || '0.00' + '元'
        }
        // 实际结算(支出)金额
        if (column.property == 'actualSettlemenExpenditure') {
          sums[index] = data.actualSettlemenExpenditureTotal && Number(data.actualSettlemenExpenditureTotal).toFixed(2) || '0.00' + '元'
        }
        // 实际结算(收入)金额/元
        if (column.property == 'actualSettlemenIncome') {
          sums[index] = data.actualSettlemenIncome && Number(data.actualSettlemenIncome).toFixed(2) || '0.00' + '元'
        }
      })

      return sums
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    activeFixed() {
      return (value) => {
        // 冻结左侧“合作主体、合作公司、收款单位、景区名称、月份”
        if (
          value == 'subjectName' ||
          value == 'companyName' ||
          value == 'payee' ||
          value == 'scenicSpotName' ||
          value == 'profitSharingMonth'
        ) {
          return 'left'
        }
      }
    },

    tableContentValue() {
      return (row, value) => {
        // console.log(row, value)
        var result = ''
        if (value == 'type') {
          // 收支类型
          if (row[value] == '1') {
            result = '收入'
          } else if (row[value] == '2') {
            result = '支出'
          } else {
            result = '无'
          }
        } else if (value == 'fenrunSettlemenType') {
          // 分润金额结算状态
          if (row[value] == '1') {
            result = '待核实'
          } else if (row[value] == '2') {
            result = '已核实'
          } else if (row[value] == '3') {
            result = '已付款/已收款'
          } else {
            result = '无'
          }
        } else if (value == 'spotSettlemenType') {
          // 配件维修费结算状态
          if (row[value] == '1') {
            result = '待核实'
          } else if (row[value] == '2') {
            result = '已核实'
          } else if (row[value] == '3') {
            result = '已付款/已收款'
          } else {
            result = '无'
          }
        } else if (
          value == 'wechatPaymentFlow' ||
          value == 'storedValuePaymentFlow' ||
          value == 'wechatStoredPaymentFlow' ||
          value == 'depositDeductionPaymentFlow' ||
          value == 'totalAmount' ||
          value == 'platformFenrunBase' ||
          value == 'platformFenrunExpenditure' ||
          value == 'platformFenrunIncome' || 
          value == 'platformStatisticsPartsMaintenance' ||
          value == 'platformStatisticsSettlementExpenditure' || 
          value == 'platformStatisticsSettlementIncome' || 
          value == 'proportion' || 
          value == 'taxPoint' ||
          value == 'actualFenrunBase' || 
          value == 'actualTaxFenrunExpenditure' || 
          value == 'actualTaxFenrunIncome' ||
          value == 'spotPartsMaintenance' ||
          value == 'actualSettlemenExpenditure' ||
          value == 'actualSettlemenIncome'
        ) {
          result = (row[value] && Number(row[value]).toFixed(2)) || '0.00'
        } else if(value == 'charge') {
          result = row[value] && (row[value] * 100).toFixed(2) + "%"
        } else {
          result = row[value] || '无'
        }
        return result
      }
    }
  }
}
</script>

